<template>
  <div class="notice-container">
    <a :href="href">{{ notice }}</a>
  </div>
</template>

<script>
import { reactive, toRefs } from "@vue/reactivity";

export default {
  setup() {
    const state = reactive({
      notice: "Checkout our upcoming projects for 2022.",
      href: "#",
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>

<style lang='scss'>
.notice-container {
  background-color: $secondary;
  display: flex;
  justify-content: center;
  align-items: center;
  a {
    color: $white;
    font-size: 14px;
    letter-spacing: 1px;
  }
}
</style>